<template>
  <li class="list-group-item" @click="selectEvent(event.id)" :class="{selected: isSelected}">
    <span class="badge" v-if="event.unread > 0">
      {{ event.unread }}
    </span>
    [{{ event.type }}] {{ event.description }}
  </li>
</template>

<script>

import {mapActions} from 'vuex'

export default {
  name: "EventEntry",
  props: [
    'event', 'isSelected'
  ],
  methods: mapActions(['selectEvent'])
}
</script>

<style scoped>
li.selected {
  font-weight: bold;
}
</style>