






























import DiscussionPost from "@/components/DiscussionPost.vue";
import {mapState} from 'vuex'
import {DiscussionPostState, EventsState, EventState} from "@/store";
import EventEntry from "@/components/EventEntry.vue";

import ckeditor from 'ckeditor4-vue';
export default {
  name: "Discussion",
  components: {DiscussionPost, EventEntry},
  computed: mapState({
    events: function (state: EventsState): Array<EventState> {
      return state.events.map(event => event.event)
    },
    currentEvent: function (state: EventsState): EventState | undefined {
      if (state.selected !== undefined) {
        return state.events.filter(event => event.event.id == state.selected).values().next().value.event;
      }
      return undefined
    },
    posts: function (state: EventsState): Array<DiscussionPostState> {
      if (state.selected !== undefined) {
        return state.events.filter(event => event.event.id == state.selected).values().next().value.discussion
      }
      return [];
    },
    selectedEvent: function (state: EventsState) {
      return state.selected;
    }

  })
}
