import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export interface UserState {
    name: string;
}

export interface DiscussionPostState {
    id: number;
    title: string;
    content: string;
    user: UserState;
}


export interface EventState {
    id: number;
    unread: number; // should be derived from the discussion posts state
    type: string;
    description: string;
}

export interface EventsState {
    events: Array<{ event: EventState; discussion: Array<DiscussionPostState> }>;
    selected?: number; // the currently selected event
}

const userManuel = {
    name: "Manuel"
};

const event0: EventState = {id: 0, unread: 5, type: "page creation", description: "Page P1 created"};
const event0Post0: DiscussionPostState = {
    id: 0,
    title: "title",
    content: "content",
    user: userManuel,
};
const event0post1: DiscussionPostState = {
    id: 1,
    title: "title 2",
    content: "content 2",
    user: userManuel,
};
const event1: EventState = {id: 1, unread: 0, type: "page update 1.1", description: "Page P1 updated"};
const event1post0: DiscussionPostState = {
    id: 0,
    title: "tadata title 2",
    content: "content 2 bis",
    user: userManuel,
};
const event1post1: DiscussionPostState = {
    id: 1,
    title: "title 2 yoodoo",
    content: "content 2 dshdkj",
    user: userManuel,
};
const state: EventsState = {
    events: [
        {
            event: event0,
            discussion: [
                event0Post0,
                event0post1
            ]
        }, {
            event: event1,
            discussion: [
                event1post0,
                event1post1
            ]
        }
    ],
    selected: 0
}
export default new Vuex.Store({
    state: state,
    mutations: {
        selectEvent(state, newEvent) {
            state.selected = newEvent;
        }
    },
    actions: {
        selectEvent({commit}, newEvent) {
            commit('selectEvent', newEvent);
        }
    },
    modules: {}
})
